@use "variables.scss" as v;

.product {
  padding: 18px 20px 25px;
  border-top: 1px solid #ebebeb;
}

.product__link {
  font-size: 16px;
  line-height: 20px;
  color: #222222;
  text-transform: uppercase;
}

.product__info-item {
  font-family: "Arial", sans-serif;
  color: #444444;
  font-size: 12px;
  line-height: 16px;
}

@media (min-width: v.$tablet-width) {
  .product {
    border: none;
  }
}
